import React from "react";
import { Row, Col, Card, Table, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import axios from "axios";
import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css"; // Import
import moment from "moment"
import Aux from "../../hoc/_Aux";
import Tracking from '../Tracking/Tracking';
import i18n from './../../i18n';

var urlbackend = require("../../env/env")();

class HistoryDriver extends React.Component {
  state = {
    Drivers: [],
    informationdriver: {}
  };

  componentDidMount() {
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Headers': 'Authorization',
        "x-access-token": localStorage.getItem('token'),
        'Authorization': 'Bearer ' + localStorage.getItem('token'),
      },
    };
    axios
      .get(urlbackend + "/driver/" + window.location.href.split("/").pop(), config)
      .then((response) => {
        console.log("this respon list", response.data);
        this.setState({ informationdriver: response.data.data[0] });
      })
      .catch((e) => {
        console.log(e);
      });

    axios
      .get(urlbackend + "/driver/history/" + window.location.href.split("/").pop(), config)
      .then((response) => {
        if (response.data) {
          console.log("this historydriver list", response.data);
          this.setState({ Drivers: response.data.data })
        }
        else {
          this.setState({ Drivers: [] })

        }

      })
      .catch((e) => {
        console.log(e);
      });
  }

  Delette(Driver) {
    console.log("hellodelet", Driver.id);
    confirmAlert({
      title: "Confirm Delete",
      message: "Are you sure to do Delete?",
      buttons: [
        {
          label: "Yes",
          onClick: () => {
            axios
              .delete("http://localhost:5000/api/v1/Drivers/" + Driver.id)
              .then(() => {
                var array = [...this.state.Drivers];
                var index = array.indexOf(Driver);
                if (index !== -1) {
                  array.splice(index, 1);
                  this.setState({ Drivers: array });
                }
              });
          },
        },
        {
          label: "No",
          onClick: null,
        },
      ],
    });
    /*  */
  }
  render() {
    return (
      <Aux>
        <Row>
          <Col>
            <Card>
              <Card.Header>
                <Card.Title as="h5">
                  <h5>{i18n.t('driver')}</h5>
                </Card.Title>
                <Card.Text>
                  <p>{i18n.t('first-name')} :{this.state.informationdriver.firstname} </p>
                  <p>{i18n.t('last-name')} :{this.state.informationdriver.lastname} </p>
                  <p>{i18n.t('username')} :{this.state.informationdriver.username} </p>
                </Card.Text>
              </Card.Header>
              <Card.Body>

                <div key={this.props.key} style={{ height: '60vh', width: '100%' }}>

                  <Tracking fortracking={"driver"} locationto={window.location.href.split("/").pop()} ></Tracking>


                </div>
                <div  >

                  <Table striped responsive>
                    <thead>
                      <tr>
                        <th>{i18n.t('title-date')}</th>
                        <th>{i18n.t('title-created-at')}</th>
                        <th>{i18n.t('title-status')}</th>
                        <th style={{ width: "10%" }}>{i18n.t('title-historic')}</th>

                      </tr>
                    </thead>
                    <tbody>
                      {this.state.Drivers.map((Driver, index) => (

                        <tr key={Driver.id}>

                          <td>
                            {moment(Driver.created_at).format("YYYY-MM-DD HH:mm:ss")}
                          </td>
                          <td>
                            {moment(Driver.created_at).format("YYYY-MM-DD HH:mm:ss")}
                          </td>
                          <td> {Driver.event} </td>
                          <td>
                            <Link to={"/history/colis/" + Driver.colis.id}>
                              <Button
                                variant="success"
                                onClick={() => console.log("not yet")}
                              >
                                {" "}
                                <span className="fa fa-file"></span>
                              </Button>
                            </Link>


                          </td>



                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </div>

              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Aux>
    );
  }
}

export default HistoryDriver;
